let Socket = ''
let setIntervalWesocketPush = null
import moment from "moment"

/**
 * websocket
 * @param {string} url ws
 */
export const createSocket = (url, func) => {
  Socket && Socket.close()
  Socket = null;
  try {
    if (!Socket) {
      console.log('websocket')
      Socket = new WebSocket(url)
      Socket.onopen = onopenWS.bind(this, func)
      Socket.onmessage = onmessageWS
      Socket.onerror = onerrorWS.bind(this, url, func)
      Socket.onclose = oncloseWS
    } else {
      console.log('websocket')
    }
  } catch (e) {
    //;
    console.log('websocket');
    onerrorWS(url, func)
  }

}

/**WS */
const onopenWS = (func, e) => {
  console.log(func, e)
  sendPing();
  func && func() // 
}

/** */
const onerrorWS = (url, func, e) => {
  console.log(url, func, e)
  Socket.close();
  clearInterval(setIntervalWesocketPush)
  console.log('', Socket, url, func)
  if (Socket.readyState !== 3) {
    Socket = null
    createSocket(url, func)
  }
}

/**WS */
export const onmessageWS = e => {
  window.dispatchEvent(new CustomEvent('onmessageWS', {
    detail: e.data
  }))
}

/**
 * 
 * @param {any} message 
 */
const connecting = message => {
  setTimeout(() => {
    if (Socket.readyState === 0) {
      connecting(message)
    } else {
      Socket.send(JSON.stringify(message))
    }
  }, 1000)
}

/**
 * 
 * @param {any} message 
 */
export const sendWSPush = message => {
  if (Socket !== null && Socket.readyState === 3) {
    Socket.close()
    createSocket()
  } else if (Socket.readyState === 1) {
    Socket.send(JSON.stringify(message))
  } else if (Socket.readyState === 0) {
    connecting(message)
  }
}

/** */
const oncloseWS = () => {
  clearInterval(setIntervalWesocketPush)
  console.log('websocket')
}
/**
 * @param {number} time  5000
 * @param {string} ping  ping
 */
export const sendPing = (time = 5000) => {
  clearInterval(setIntervalWesocketPush)
  Socket.send(JSON.stringify({
    type: "heart",
    time: moment().format('YYYY-MM-DD HH:mm:ss')
  }))
  setIntervalWesocketPush = setInterval(() => {
    Socket.send(JSON.stringify({
      type: "heart",
      time: moment().format('YYYY-MM-DD HH:mm:ss')
    }))
  }, time)
}

// websocket
export const closeWS = () => {
  Socket && Socket.close();
  Socket = null;
  console.log('websocket')
}